<template>
<div id = "index" style="height: 100%">
  <div id="TypeSwitch">
        <strong @click="openList()">类型筛选</strong>
        <ul :style="{'overflow':'auto','height': '20rem','display':display}" >
          <li class="search"><el-input style="padding:5%" placeholder="请输入关键词" size="mini" v-model="search" clearable></el-input></li>
          <li :class="{'current':!selectModle}" @click="moreSelect('one')">单选</li>
          <li :class="{'current':selectModle}" @click="moreSelect('more')">多选</li>
          <template v-for="item in markerCatalog">
            <li class="title">{{item.name}}</li>
            <li v-for="chil in item.children" :data-type="chil.id"  :class="['icon-'+chil.img,{'current':typeSelect[chil.id]}]" @click="toggleVisible(chil.id,$event)">{{chil.name}}</li>
        </template>
        </ul>
        
    </div>
    <div id="NameSearch">
      
        <!-- <input id="KeyWords" type="button" value="" v-model="search" placeholder="搜索" />
        <span id="ClearKeyword" @click="clearSearch()" class="clear">清除</span> -->
        
        <el-button type="info" size="small" @click="login()">{{loginSign}}</el-button>
    </div> 

    <div  id = "info"><a href="#" @click="$router.push('sponsor')">求支援！|</a><a href="#"  @click="$router.push('appdownload')"> APP下载</a></div>
    

<div id="MapContainer"  >

</div>

</div>

  
</template>

<script>
import Vue from 'vue'
import AV from 'leancloud-storage'
import 'assets/css/Main.css'
import 'assets/css/New.css'
import 'assets/css/type.css'
import 'assets/css/side.css'
import { MarkerCatalog } from './Catalog.js'
import { MarkerData } from './cnMarker.js'
import { ShrinesToJapanese } from './ShrinesJapanese'
import store from '../store'
import markInfo from './markInfo'
import router from '../router'
export default {
  components: {
    markInfo
  },
  name: 'index',
  data: function () {
    return {
      mapData: {
        zoom: 2,
        center: [-116.25, 127.625],
        minZoom: 2,
        maxZoom: 7,
        bounds: new L.LatLngBounds(new L.LatLng(-49.875, 34.25), new L.LatLng(-206, 221)),
        crs: L.CRS.Simple,
        //url: 'http://file.qingxinchengming.cn//Map/{z}_{x}_{y}.png'  //七牛云
        url: 'http://map-1251768480.file.myqcloud.com/map/{z}_{x}_{y}.png'  //腾讯云
        //url: 'Map/{z}_{x}_{y}.png'
      },
      map: {},
      markerCatalog: MarkerCatalog,
      typeListIndex: '',
      markerStyle: {},//图标样式
      visibleMarker: {},//是否显示点
      cacheMarker: [],//点位缓存
      markerGroup: [],
      search: '',
      display: 'none',
      loginSign: '登录',
      currentUser: null, //用户信息
      markObj: AV.Object.extend('markObj'),  //声明mark对象
      infoHtml: '<div><p>网站是我在闲暇之余制作的，由于制作水平有限，难免会有很多的问题，希望大家多多包涵。</p>' +
        '<p>由于流量人数增加，之前服务器和CDN的免费流量已经用完了，如果本网站提升了您的游戏体验</p>' +
        '<p>还需要您的支持和帮助。</p>' +
        '<img src="static/wx.png"><br>' +
        '<img src="static/zfb.jpg"><div>',
      textUpdate: '<p>同学们年都过的怎么样啊？现在开始工作了，时间也越来越少了，我会争取更新更多的功能的。。。。' +
        '<p>0.3.1<p>更新了点位评论和点赞功能，在解谜的过程中要和别人分享自己的方法哦，大家一起才能更快乐。<p>目前功能还比较简陋，还有很多样式问题，大家先凑合用，我会尽快更新完善的评论功能，争取能够上传图片等附件。<p>0.2.11<p>解禁了搜索功能，使用关键词就能搜索到相应的点位，支持中英文搜索。',
      typeSelect: [],  //选择的类型
      selectModle: false, //多选模式
    }
  },
  methods: {
    test(data) {
      const TestObject = AV.Object.extend('TestObject');
      const testObject = new TestObject();
      testObject.save({ words: 'Hello World!' });
      alert('LeanCloud Rocks!');
    },
    openInfoL() {
      var that = this
      this.$message({
        dangerouslyUseHTMLString: true,
        center: true,
        showClose: true,
        duration: 0,
        message: that.infoHtml
      });
    },
    getMapObject() {

      //this.bounds = this.map.getBounds()
      //let bounds = new L.latLngBounds(new L.LatLng(-50, 327.75), new L.LatLng(-182.75, -72.25))
      //this.map.setMaxBounds(bounds)

    },
    updateInfo() {
      var that = this
      this.$message({
        dangerouslyUseHTMLString: true,
        showClose: true,
        duration: 4000,
        message: that.textUpdate,
        type: 'success'
      });
    },
    getCenter() {

    },

    initMap() {
      //let bounds = new L.LatLngBounds(new L.LatLng(-49.875, 34.25), new L.LatLng(-206, 221));
      this.map = L.map('MapContainer', {
        crs: this.mapData.crs,
        attributionControl: true,
        maxBounds: this.mapData.bounds,
        maxBoundsViscosity: 1.0,
      }).setView([0, 0], 2);
      let layer = L.tileLayer(this.mapData.url, {
        attribution: '<a href=mailto:work@songyan.xyz>@倾心成铭</a>',
        minZoom: 2,
        maxZoom: 7,
        noWrap: true,
        bounds: this.mapData.bounds
      }).addTo(this.map);

    },
    /**
     * 循环处理数据
     */
    handleMarkData() {
      let tmp = this.markerCatalog
      for (let i = 0; i < tmp.length; i++) {
        for (let z = 0; z < tmp[i].children.length; z++) {
          this.markerStyle[tmp[i].children[z].id] = tmp[i].children[z].img
          this.visibleMarker[tmp[i].children[z].id] = false
        }
      }
    },
    toggleVisible(type, event) {
      this.typeListIndex = type
      let selectModle = this.selectModle
      if ((event != null && event.ctrlKey) || selectModle) {
        if (this.visibleMarker[type]) {
          this.visibleMarker[type] = false;
          this.typeSelect[type] = false
        } else {
          this.visibleMarker[type] = true;
          this.typeSelect[type] = true
        }
      } else {

        for (const key in this.visibleMarker) {
          if (this.visibleMarker.hasOwnProperty(key)) {
            this.visibleMarker[key] = false
            this.typeSelect[key] = false

          }
        }
        this.visibleMarker[type] = true;
        this.typeSelect[type] = true
      }

      Vue.set(this.visibleMarker, type, this.visibleMarker[type]);
      Vue.set(this.typeSelect, type, this.typeSelect[type]);
      this.refreshMarker("filter");
      //console.log(this.visibleMarker[type])
    },
    /**
     * 刷新点位
     * @augments ddd from 
     * 
     */
    refreshMarker(from) {
      let router = this.$router
      //console.log('123',this)
      this.$store.commit('setRouterObj',router)
      if (this.cacheMarker.length > 0) {
        this.markerGroup.clearLayers();
      }
      this.cacheMarker.splice(0, this.cacheMarker.length);//
      this.cacheMarker = []
      this.map.rem
      for (let i = 0; i < MarkerData.length; i++) {
        let visible = false
        if (from === "filter" && this.visibleMarker[MarkerData[i].markerCategoryId]) { visible = true }
        if (from === "search") {  //这是搜索功能
          let keyword = this.search;
          if (MarkerData[i].name.toLowerCase().replace(/^\s+|\s+$/g, "").indexOf(keyword.toLowerCase().replace(/^\s+|\s+$/, "")) !== -1) visible = true;
          if (MarkerData[i].description.toLowerCase().replace(/^\s+|\s+$/g, "").indexOf(keyword.toLowerCase().replace(/^\s+|\s+$/, "")) !== -1) visible = true;
        }
        if (visible) {
          let key
          let markId
          if (MarkerData[i].enName != null) {
            key = MarkerData[i].markerCategoryId + "-" + MarkerData[i].id + "-" + MarkerData[i].enName.replace(/[^A-Z]/gi, "-");
            markId = MarkerData[i].markerCategoryId + "-" + MarkerData[i].id
          } else {
            key = MarkerData[i].markerCategoryId + "-" + MarkerData[i].id + "-" + MarkerData[i].name.replace(/[^A-Z]/gi, "-");
            markId = MarkerData[i].markerCategoryId + "-" + MarkerData[i].id
          }

          let popupHtml = '<div class="popupContainer" id = "popupDiv' + key + '">';
          if (MarkerData[i].enName != null && MarkerData[i].markerCategoryId != '1934' && MarkerData[i].markerCategoryId != '1916') {
            popupHtml += '<strong class="name">' + MarkerData[i].name + '(' + MarkerData[i].enName + ')</strong>';
            popupHtml += '<div class="buttonContainer">';
            popupHtml += '<span class="markButton" @click="MarkPoint(\'' + key + '\')" data-key="' + key + '">标记</span>';
            popupHtml += '<a class="markButton" target="_blank" @click="markerInfo(\'' + markId + '\',\''+MarkerData[i].name+'\')">攻略</a>';
            popupHtml += '<a class="markButton" target="_blank" href="http://www.ign.com/search?q=' + encodeURIComponent(MarkerData[i].enName) + '">IGN</a>';
            
            popupHtml += '<a class="markButton" target="_blank" href="https://c.gufen.ga/#q=' + encodeURIComponent(MarkerData[i].enName) + '">Google</a>';
            popupHtml += '<a class="markButton" target="_blank" href="http://www.baidu.com/baidu?word=' + encodeURIComponent(MarkerData[i].enName + ' ' + MarkerData[i].name) + '">百度</a>';
            popupHtml += '<a class="" target="_blank" @click="editRaiders(\'' + markId + '\')">提交攻略</a>';
          }
          else {
            popupHtml += '<strong class="name">' + MarkerData[i].name + '</strong>';
            popupHtml += '<div class="buttonContainer">';
            
            popupHtml += '<span class="markButton" @click="MarkPoint(\'' + key + '\')" data-key="' + key + '">标记</span>';
            popupHtml += '<a class="markButton" target="_blank" @click="markerInfo(\'' + markId + '\')">攻略</a>';  
            popupHtml += '<a class="markButton" target="_blank" href="http://www.ign.com/search?q=' + encodeURIComponent(MarkerData[i].name) + '">IGN</a>';  
            popupHtml += '<a class="markButton" target="_blank" href="https://c.gufen.ga/#q=' + encodeURIComponent(MarkerData[i].name) + '">Google</a>';
            popupHtml += '<a class="markButton" target="_blank" href="http://www.baidu.com/baidu?word=' + encodeURIComponent(MarkerData[i].name) + '">百度</a>';
            popupHtml += '<a class="" target="_blank" @click="editRaiders(\'' + markId + '\')">提交攻略</a>';
          }
          popupHtml += '</div>';

          if (MarkerData[i].markerCategoryId === "1925") {
            if (ShrinesToJapanese[MarkerData[i].enName]) {
              let jName = ShrinesToJapanese[MarkerData[i].enName];
              popupHtml += '<strong class="name">' + jName + '</strong>';
              popupHtml += '<div class="buttonContainer">';
              popupHtml += '<a class="markButton" target="_blank" href="https://zelda-bow.gamepedia.jp/?s=' + jName + '">GamePedia</a>';
              popupHtml += '<a class="markButton" target="_blank" href="http://wiki2.h1g.jp/zelda_bow/index.php?' + jName + '">H1G</a>';
              popupHtml += '<a class="markButton" target="_blank" href="https://c.gufen.ga/#q=' + jName + '">Google</a>';
              popupHtml += '<a class="markButton" target="_blank" href="http://www.baidu.com/baidu?word=' + jName + '">百度</a>';
              popupHtml += '</div>';
            } else {
              console.log("no find shrine janpanese: " + MarkerData[i].enName);
            }
          }

          popupHtml += '</div>';

          let className = "mark-" + key;
          //console.log('33344',key)
          if (localStorage.getItem(key) == 1) {  //==1
            className += " marked";
          }
          className += " markIcon";
          className += " icon-" + this.markerStyle[MarkerData[i].markerCategoryId];
          let marker = L.marker([MarkerData[i].y, MarkerData[i].x], {
            title: MarkerData[i].name,
            icon: L.divIcon({
              className: className,
              iconSize: [20, 20],
              iconAnchor: [10, 10],
              popupAnchor: [0, -10],
            }),
            attribute: key
          }).bindPopup(popupHtml)//.addTo(this.map)//.bindPopup(popupHtml);

          marker.on('click', function (e) {
            
            let key = this.options.attribute
            //console.log("233", this)
            if (marker.isPopupOpen()) {
              //console.log('1222');
              new Vue({
                el: '#popupDiv' + key,
                store,
                data: function () {
                  return {
                    test: '1111',
                    markObject: AV.Object.extend('markObj')  //声明mark对象
                  }
                },
                methods: {
                  MarkPoint(key) {
                    var mark
                    let oldValue = localStorage.getItem(key);
                    if (oldValue == null) {  //不存在
                      oldValue = 0
                      if (AV.User.current() != null) {  //登录状态
                        var user = AV.User.current().attributes.username;  //不存在就创建一个信息
                        mark = new this.markObject()
                        mark.set('userName', user)
                        mark.set('key', key)
                      }

                    } else {  //已经存在这个点位了

                      let objId = localStorage.getItem('id' + key)
                      if (AV.User.current() != null) {
                        mark = AV.Object.createWithoutData('markObj', objId);
                      }

                    }
                    let newValue
                    if (oldValue == 0) {
                      newValue = 1
                    } else if (oldValue == 1) {
                      newValue = 0
                    }
                    localStorage.setItem(key, newValue == 1 ? 1 : 0);
                    let docu = document.querySelector('#MapContainer .leaflet-marker-pane .mark-' + key)
                    if (docu.className.indexOf('marked') > -1) {
                      docu.classList.remove("marked")
                    } else {
                      docu.classList.add("marked")
                    }
                    if (AV.User.current() != null) {
                      mark.set('value', newValue)
                      mark.save().then(function (todo) {
                        //console.log('objectId is ' + todo.id);
                        localStorage.setItem('id' + key, todo.id);
                      }, function (error) {
                        console.error(error);
                      });
                    }

                    //console.log('111', user)

                    //console.log(docu)
                  },
                  markerInfo(id,name) {
                    //console.log(id)
                    this.$store.dispatch('getMarkInfo', id)
                    this.$layer.iframe({
                      content: {
                        content: markInfo, //传递的组件对象
                        parent: this,//当前的vue对象
                        data: []//props
                      },
                      area: ['85%', '65%'],
                      title: name
                    });
                  },
                  editRaiders(id){
                    router.push({name:'editRaider',params:{id:id}})
                  }
                }
              })
            }
          })

          this.cacheMarker.push(marker);

        }

      }
      this.markerGroup = L.layerGroup(this.cacheMarker)
      this.map.addLayer(this.markerGroup);

    },
    clearSearch() {
      this.search = ''
    },
    MarkPointMarkPoint(mark) {
      console.log(mark)
    },
    refreshFilter() {
      let allVisible = true;
      let allHidden = true;
      for (let o in this.visibleMarker) {
        if (visibleMarker.hasOwnProperty(o)) {
          if (!visibleMarker[o]) {
            allVisible = false;
          } else {
            allHidden = false;
          }
        }
      }
    },
    openList() {
      if (this.display != 'block') {
        this.display = 'block'
      } else {
        this.display = 'none'
      }
    },
    login() {
      this.currentUser = AV.User.current();
      if (this.currentUser) {
        //console.log("1111", this.currentUser)
        this.logout()
      }
      else {
        this.$router.push({
          path: 'info'
        })
      }
    },
    getUserName() {

      this.currentUser = AV.User.current()
      if (this.currentUser != null) {
        this.loginSign = this.currentUser.attributes.username
      } else {
        this.loginSign = '登录'
      }

    },
    logout() {

      this.$confirm('是否注销登录?注销后会清空所有的本地点位信息!', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        localStorage.clear()
        AV.User.logOut();
        // 现在的 currentUser 是 null 了
        this.currentUser = AV.User.current();
        this.loginSign = '登录'
        this.$message({
          type: 'success',
          message: '注销成功!',
        });
      }).catch(() => {

      });
    },
    /**@augments
     * 多选
     */
    moreSelect(type) {
      if (type == 'one') {
        this.selectModle = false
      }
      if (type == 'more') {
        this.selectModle = true
      }

    },
    getInfoJson(){
      let that = this
      // this.axios.get('https://file-cos-1256180278.cos.ap-chengdu.myqcloud.com/updateInfo/update.json').then((data) =>{
      //   //console.log(data)
      //   that.textUpdate = data.data.update
      //   this.updateInfo()
      // })
      let query = new AV.Query("updateInfo");
      query.get('5aa2437d9545040065e2b8fa').then(todo => {
        
        that.textUpdate = todo.attributes.updateInfo
        this.updateInfo()
      })
    }




  },
  created() {
    //this.initLC()
  },
  mounted() {
    //this.getMapObject()
    //console.log(this.markObj)
    this.initMap()
    this.handleMarkData()
    this.getUserName()
    
    this.getInfoJson()
  },
  watch: {
    search: function (newWord) {
      if (newWord == '') {
        //this.toggleVisible("1925", null)
      } else {
        this.refreshMarker('search')
      }

    }
  }

}
</script>

<style>

</style>



